.nav-button {
  text-transform: uppercase;
  position: absolute;
  right: 1rem;
  top: 0.8rem;
  z-index: 89;
}
@media (min-width: 1080px) {
  .other-menu-wrapper {
    display: none;
  }
}

@media (max-width: 360px) {
  .nav-button {
    top: 0.6rem;
  }
}
.nav-wrapper {
  background-color: #f9f9f9;
  box-shadow: -6px 0px 12px -8px rgba(0, 0, 0, 0.3);
  padding-top: 7rem;
  perspective: 1000px;
  position: fixed;
  height: calc(100vh - 80px);
  right: 0;
  top: 0;
  transform: translateX(100%);
  transition: transform 0.6s, visibility 0.6s;
  visibility: hidden;
  max-width: 18rem;
  width: 60vw;
  min-width: 15rem;
  z-index: 79;
}
.nav-visible .nav-wrapper {
  transform: translateX(0);
  visibility: visible;
}
.nav {
  position: relative;
  transform: rotateY(90deg);
  transform-origin: left center;
  transition: transform 0.6s;
  padding-left: 0;
}
.nav-visible .nav {
  transform: rotateY(0deg);
}
.nav li.active a {
  background-color: #efefef;
}
.nav a {
  display: block;
  line-height: 1.4;
  padding: 0.7em 0.5em 0.7em 1.4em;
  text-transform: uppercase;
}
.nav a:focus,
.nav a:hover {
  background-color: rgba(46, 152, 217, 0.15);
  text-decoration: none;
}
.nav-marker {
  background-color: currentColor;
  height: 2.8rem;
  position: absolute;
  right: 0;
  top: 80px;
  width: 3px;
}
.nav-marker:before {
  border: 5px solid transparent;
  border-right-color: inherit;
  content: '';
  height: 0;
  position: absolute;
  right: 3px;
  top: 50%;
  transform: translateY(-50%);
  width: 0;
}
.color-1 {
  color: #efefef;
}
.button-lines {
  background: transparent;
  border: none;
  cursor: pointer;
  display: inline-block;
  padding: 1rem 0.5rem;
  user-select: none;
}
.lines {
  background: #666;
  border-radius: 0.2rem;
  display: inline-block;
  height: 0.2rem;
  width: 2rem;
  position: relative;
}
.lines:hover {
  background: #666;
}
.lines:after,
.lines:before {
  background: #666;
  border-radius: 0.2rem;
  display: inline-block;
  height: 0.2rem;
  width: 2rem;
  content: '';
  left: 0;
  position: absolute;
}
.lines:after:hover,
.lines:before:hover {
  background: #666;
}
.lines:after {
  top: 0.5rem;
}
.lines:before {
  top: -0.5rem;
}
.button-lines-x .lines {
  transition: background 0.3s ease;
}
.button-lines-x .lines:after,
.button-lines-x .lines:before {
  transform-origin: 50% 50% 0;
  transition: top 0.3s 0.4s ease, transform 0.3s ease;
}
.nav-visible .button-lines-x .lines {
  background: transparent;
}
.nav-visible .button-lines-x .lines:after,
.nav-visible .button-lines-x .lines:before {
  top: 0;
  transition: top 0.3s ease, transform 0.3s 0.3s ease;
}
.nav-visible .button-lines-x .lines:after {
  transform: rotate3d(0, 0, 1, 45deg);
}
.nav-visible .button-lines-x .lines:before {
  transform: rotate3d(0, 0, 1, -45deg);
}
li {
  list-style: none;
}
.nav-list a {
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-align: left;
  margin-left: 0.8rem;
  margin-right: 0.8rem;
  margin-bottom: 0.3rem;
  font-weight: 200;
}
